'use client'
import { useEffect, useState } from "react";
import HeroSection from "./components/HeroSection";
import TabsNavigation from "./components/TabsNavigation";
import PoolRankTable from "./components/PoolRankTable";
import ScoresSearch from "./components/ScoresSearch";
import LiveLeaderboardTable from "./components/LiveLeaderboardTable";
import { calculatePlayerScore } from "../js/golf";
import { cachedData } from "./api/aws/retrieveGolfData/route";

export default function Home() {
  const [activeTab, setActiveTab] = useState("scoreLeaderboard");
  const { tournamentData, picksData } = cachedData;

  console.log("Tournament Data:", tournamentData);
  console.log("Picks Data:", picksData);

  return (
    <div>
      <HeroSection />
      <TabsNavigation activeTab={activeTab} setActiveTab={setActiveTab} />
      <div className="tab-content">
        {activeTab === "scoreLeaderboard" && <PoolRankTable players={picksData || []} />}
        {activeTab === "scores" && <ScoresSearch players={picksData || []} />}
        {activeTab === "leaderboard" && <LiveLeaderboardTable leaderboard={tournamentData?.results?.leaderboard || []} />}
      </div>
    </div>
  );
}
